import React from "react";

export default function About() {
  return (
    <>
      <section className="section_about">
        <h2 className="about">About</h2>
        <p className="about__text">
          I am a front-end developer with a focus on Shopify. 
          I believe Shopify to offer both their customers and developers great tools to make the end-user experience smooth and feature-rich. 
          I am currently working full time but am always available to discuss potential projects/collaborations.
        </p>
      </section>
    </>
  );
}
